import React, { useEffect, useRef } from 'react';
import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';

const SatelliteConstellation = () => {
  const mountRef = useRef(null);

  useEffect(() => {
    const scene = new THREE.Scene();
    const camera = new THREE.PerspectiveCamera(75, 1, 0.1, 1000);
    const renderer = new THREE.WebGLRenderer();
    renderer.setSize(400, 400);
    mountRef.current.appendChild(renderer.domElement);

    // Earth
    const earthGeometry = new THREE.SphereGeometry(1, 32, 32);
    const earthMaterial = new THREE.MeshBasicMaterial({ color: 0x2233ff });
    const earth = new THREE.Mesh(earthGeometry, earthMaterial);
    scene.add(earth);

    // Satellites in polar orbit (97.3° inclination)
    const satelliteGeometry = new THREE.SphereGeometry(0.02, 8, 8);
    const satelliteMaterial = new THREE.MeshBasicMaterial({ color: 0xff0000 });

    const inclination = (97.3 * Math.PI) / 180; // Convert degrees to radians
    const radius = 1.5; // Orbit radius (can adjust)

    for (let i = 0; i < 10; i++) {
      const angle = (i / 10) * Math.PI * 2;
      const satellite = new THREE.Mesh(satelliteGeometry, satelliteMaterial);
      satellite.position.set(
        radius * Math.cos(angle),
        radius * Math.sin(angle) * Math.sin(inclination),
        radius * Math.sin(angle) * Math.cos(inclination)
      );
      scene.add(satellite);
    }

    // Ground stations at 87°N and 87°S
    const stationGeometry = new THREE.SphereGeometry(0.03, 8, 8);
    const stationMaterial = new THREE.MeshBasicMaterial({ color: 0x00ff00 });

    const northLat = 87 * (Math.PI / 180);
    const southLat = -87 * (Math.PI / 180);

    // North ring (87°N)
    for (let i = 0; i < 5; i++) {
      const angle = (i / 5) * Math.PI * 2;
      const northStation = new THREE.Mesh(stationGeometry, stationMaterial);
      northStation.position.set(
        Math.cos(northLat) * Math.cos(angle),
        Math.sin(northLat),
        Math.cos(northLat) * Math.sin(angle)
      );
      scene.add(northStation);
    }

    // South ring (87°S)
    for (let i = 0; i < 5; i++) {
      const angle = (i / 5) * Math.PI * 2;
      const southStation = new THREE.Mesh(stationGeometry, stationMaterial);
      southStation.position.set(
        Math.cos(southLat) * Math.cos(angle),
        Math.sin(southLat),
        Math.cos(southLat) * Math.sin(angle)
      );
      scene.add(southStation);
    }

    camera.position.z = 3;

    const controls = new OrbitControls(camera, renderer.domElement);

    const animate = () => {
      requestAnimationFrame(animate);
      controls.update();
      renderer.render(scene, camera);
    };
    animate();

    return () => {
      mountRef.current.removeChild(renderer.domElement);
    };
  }, []);

  return <div ref={mountRef} />;
};

export default SatelliteConstellation;
